.navbar {
  width: 100%;
  height: 43px;
  display: flex;
  background-color: #3b7adc;
  align-items: center;
}

.menuButton {
  margin-left: 5px;
  background-color: #e5e5e500;
  border-radius: 4px;
  border: 0;
  cursor: pointer;
  position: relative;

  &:hover {
    background-color: #e5e5e536;
  }

  height: 32.4px;
  width: 32.4px;
}

.menuIcon {
  font-size: 30px;
}

.homeButton {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 8px;
  background-color: #e5e5e500;
  border-radius: 4px;
  border: 0;
  cursor: pointer;
  position: relative;
  height: 32.4px;
  width: 32.4px;

  &:hover {
    background-color: #e5e5e536;
  }
}

.homeIcon {
  font-size: 25px;
}